.card {

  display: flex;
  flex-direction: column;
  position: relative;
  border: 1px solid var(--color-ebony);
  border-radius: 15px;
  padding: rem(22) rem(23) rem(22) rem(20);
  background-color: transparent;
  transition: color 0.3s linear,
    background-color 0.3s linear,
    border-color 0.3s linear;

  // @include for-width(-desktop-s) {
  //   padding: clamp(10px, 1.2rem, 20px);
  // }

  &::before {
    content: "";
    position: absolute;
    right: rem(25);
    bottom: rem(26);
    mask-image: url("../img/sprite.svg#cursor");
    mask-position: center;
    mask-repeat: no-repeat;
    mask-size: cover;
    width: 27px;
    height: 27px;
    background-color: var(--color-ebony);
    transition: background-color 0.3s linear;
  }

  &:hover {
    color: var(--color-white);
    background-color: var(--color-ebony);
    border: 1px solid var(--color-white);

    &::before {
      background-color: var(--color-white);
    }

    .card__icon::before {
      background-color: var(--color-white);
    }

  }

  &__icon {
    @include flex-center;

    position: relative;
    margin-bottom: 15px;
    min-height: 104px;

    &::before {
      content: "";
      position: absolute;
      mask-image: url("../img/sprite.svg#infinity");
      mask-position: center;
      mask-repeat: no-repeat;
      mask-size: cover;
      width: 70px;
      height: 70px;
      background-color: var(--color-ebony);
      transition: background-color 0.3s linear;
    }

    &--digital {
      &::before {
        mask-image: url("../img/sprite.svg#infinity");
        height: 33px;
      }
    }

    &--seo {
      &::before {
        mask-image: url("../img/sprite.svg#triangle");
        width: 60px;
        height: 45px;
      }
    }

    &--smm {
      &::before {
        mask-image: url("../img/sprite.svg#insta");
        width: 50px;
        height: 50px;
      }
    }

    &--advertising {
      &::before {
        mask-image: url("../img/sprite.svg#double-arrow");
        width: 55px;
        height: 45px;
      }
    }

    &--landing {
      &::before {
        mask-image: url("../img/sprite.svg#infinity");
      }
    }

    &--multipage {
      &::before {
        mask-image: url("../img/sprite.svg#infinity");
      }
    }
  }

  h3 {
    margin-bottom: 0;
    padding-left: clamp(25px, 5.6rem, 90px);
  }

  p {
    margin-bottom: rem(22);
  }

  a {
    &::before {
      content: "";
      position: absolute;
      z-index: 2;
      inset: 0;
    }
  }

  span:not([class]) {
    @include adaptive_size("font-size", 40, 25);

    font-weight: 900;
    line-height: 1.3;
    letter-spacing: 0.06em;
  }

  &__content {
    margin-top: auto;

    @include flex-center;
  }

  &__up {
    margin-right: clamp(5px, 3rem, 50px);
  }
}